<template>
    <div>
        <top-nav title="用户提现"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是用户提现申请列表</p>
            <p>2).用户提交了提现申请将会直接扣除余额，这里的操作仅记录商家打款等操作，具体事宜商家和用户自行沟通</p>
            <p>3).同意提现系统并不会直接转账打款给用户(为保障财产安全)，请线下自行转账等操作并在申请列表中备注.</p>
        </div>

        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="提现状态">
                        <el-select v-model="search.status" placeholder="订单状态">
                            <el-option label="全部状态" :value="-999"></el-option>
                            <el-option v-for="item in datas.withdraw_status_mean" :label="item.value" :value="item.key" :key="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="用户ID">
                        <el-input v-model="search.user_id"></el-input>
                    </el-form-item>

                    <el-form-item label="申请日期">
                        <date-between  v-model="search.date"></date-between>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="mini" type="primary" @click="getList(1)" icon="el-icon-search">搜索</el-button>
                    </el-form-item>

                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column width="80" prop="apply_id" label="ID"></el-table-column>

                    <el-table-column label="用户ID" width="150">
                        <template slot-scope="scope">
                            用户ID：{{scope.row.user_id}} |
                            <router-link :to="'/customer/view/'+scope.row.user_id">
                                <el-button type="text" >跟踪</el-button>
                            </router-link>
                        </template>
                    </el-table-column>

                    <el-table-column label="用户信息" width="200">
                        <template slot-scope="scope">
                            <p>昵称: {{scope.row.nickname}}</p>
                            <p>手机: {{scope.row.mobile}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column  label="申请提现金额" width="150">
                        <template slot-scope="scope">
                            <span class="wyu-color-red">￥ {{scope.row.money}}</span>
                        </template>
                    </el-table-column>

                    <el-table-column label="当前状态" width="120">
                        <template slot-scope="scope">
                            {{scope.row.status_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="备注信息" >
                        <template slot-scope="scope">
                            <div class="ellipsis-03">{{scope.row.remark}}</div>
                            <!--<p v-if="scope.row.remark"><el-button type="text" >查看详情</el-button></p>-->
                        </template>
                    </el-table-column>

                    <el-table-column prop="add_time" label="申请时间" width="150"></el-table-column>

                    <el-table-column label="操作" width="200">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <el-button type="text" v-if="scope.row.status == 0" @click="dialog=true,form.apply_id=scope.row.apply_id">提现处理</el-button>
                                <el-button type="text" v-if="scope.row.status > 0">已处理</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="getList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </div>

        <el-dialog title="处理提现" :visible.sync="dialog" width="700px" center :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="subLoading">
                <el-form-item label="处理状态">
                    <el-select v-model="form.status" placeholder="处理状态">
                        <el-option v-for="item in datas.withdraw_status_mean" :label="item.value" :value="item.key" :key="item.key" ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注"  >
                    <el-input type="textarea" :rows="5" v-model="form.remark" ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog= false;subLoading = false;">取 消</el-button>
                    <el-button size="mini" type="primary" @click="handle()">确 定</el-button>
                </el-form-item>
            </el-form>

        </el-dialog>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import DateBetween from "../../../components/DateBetween";
    export default {
        name: "customerIndex",
        components: {DateBetween, TopNav},
        data: function () {
            return {
                //整体菊花
                select:1,
                loading: false,
                subLoading: false,
                dialog: false,
                search:this.initSearch(),
                status:{},
                datas:{
                    withdraw_status_mean: {},
                },
                form: {
                    'apply_id': 0,
                    'status': 0,
                    'remark': '',
                },
                userList:[],
            };
        },
        created() {
            this.getList();
        },
        methods: {
            initSearch(){
                return {
                    date:[],
                    user_id:'',
                    status: -999,
                }
            },

            //获取列表
            getList(page=1,init=false){
                //this.search = this.initSearch();
                this.loading = true;
                api.post("/shop/admin.finance.withdraw/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    end_add_time:this.search.date[1] ? this.search.date[1] :  0,
                    bg_add_time:this.search.date[0] ? this.search.date[0] : 0,
                    user_id:this.search.user_id,
                    status:this.search.status,
                    type: 1,
                }).then(res => {
                    this.loading = false;
                    this.datas = res;
                }).catch(() => {
                    this.loading = false;
                });
            },

            //处理申请，同意并打款或拒绝
            handle() {
                this.subLoading = true;
                if (this.form.apply_id == 0) {
                    this.$message({
                        message: '参数错误',
                        type: 'error',
                    });
                    this.form = {};
                    this.subLoading = false;
                    return false;
                }
                if (this.form.remark == '') {
                    this.$message({
                        message: '备注必须填写',
                        type: 'error',
                    });
                    this.form = {};
                    this.subLoading = false;
                    return false;
                }
                api.post("/shop/admin.finance.withdraw/handle", {
                    appToken: this.$store.getters.appToken,
                    apply_id: this.form.apply_id,
                    status: this.form.status,
                    remark: this.form.remark,
                }).then(() => {
                    this.$message({
                        message: '操作成功！',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.dialog = false;
                    this.getList();
                }).catch(() => {
                    this.subLoading = false;
                });
            },

        }
    };
</script>
